html,
body {
  background-color: #ffffff;
  font-family: "Titillium Web", sans-serif;
  max-width: 100%;
  overflow-x: hidden;
}

.col-lg-6.intro-content {
  margin-top: 200px;
  /* margin-left: 250px; */
}

.intro-content h1 {
  font-size: 80px;
  color: #18221c;
  /* text-shadow: 2px; */
}

.intro-content p {
  font-size: 26px;
  margin-top: 20px;
  color: #ffffff;
}

.intro-content hr {
  height: 3px;
  width: 350px;
  background-color: #ffffff;
}

.intro-content .second {
  width: 150px;
}

.col-lg-6.intro-content b {
  font-size: 35px;
}

.col-lg-6.intro-content h1:hover {
  text-shadow: black;
  /* -webkit-animation: random 5s infinite; */
  /* animation: random 5s infinite; */
}

.home {
  /* background-color: #9c975d; */
  box-shadow: 0px 4px 5px -3px black;
  width: 100%;
  min-height: 700px;
}

.home {
  background-position: center;
  text-align: center;
  background: linear-gradient(-25deg, #5289ee, #c8dcfc);
  /* margin-bottom: 50px; */
}

.social-links a li {
  margin-left: 20px;
  color: #ffffff;
}

.social-links a:hover {
  cursor: pointer;
  color: rgb(31, 51, 35);
  text-shadow: 2px 2px 20px black;
  font-weight: 800;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: black;
  opacity: 50%;
}

::-webkit-scrollbar-track {
  background-color: lightgray;
}

@media only screen and (max-width: 450px) {
  .intro-content hr {
    width: 100% !important;
  }

  .col-lg-6.intro-content {
    margin-top: 100px;
  }

  .col-lg-6.intro-content h1 {
    font-size: 35px;
  }

  .col-lg-6.intro-content b {
    font-size: 25px;
  }

  .col-lg-6.intro-content p {
    font-size: 18px;
  }
}